import Vue from "vue";
import VueRouter from "vue-router";
import PageLayout from "../layouts/PageLayout.vue";
import Home from "../views/Page/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: PageLayout,
    redirect: "/home"
  },
  {
    path: "/login",
    name: "Login",
    component: () =>
      import(/* webpackChunkName: "group-login" */ "../views/Page/Login.vue")
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "group-about" */ "../views/Page/About.vue")
  },
  {
    path: "/home",
    name: "Home",
    component: Home
  }
];

const router = new VueRouter({
  routes
});

export default router;
